import * as THREE from 'three'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'
import './style.css'

const scene = new THREE.Scene()
const cam = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000 )
cam.position.z = 3

const renderer = new THREE.WebGLRenderer()
renderer.setSize(window.innerWidth, window.innerHeight)
document.body.appendChild(renderer.domElement)

const spotLight = new THREE.SpotLight( 0xffffff, 20 )
spotLight.position.set( 0, 3, 0 )
scene.add( spotLight )

const button = document.getElementById('katanaOut')

const retultText = document.getElementById('resultText')

const manCont = document.querySelector('.manualTwo')

const retry = document.getElementById('retry')

const loader = new GLTFLoader()
loader.load('models/rock_sword.glb', (gltf) => {
  scene.add(gltf.scene)
  let sword = gltf.scene.children[1]
  console.log(gltf.scene.children[1])
  button.addEventListener('click', (e) => {
    e.preventDefault()
    manCont.classList.add('hidden')
    if (random() === 1) {
      move(sword)
      retultText.innerText = 'You win!'
    }
    else {
      retultText.innerText = 'You loose'
    }
    button.classList.add('hidden')
    retry.classList.remove('hidden')
    retry.addEventListener('click', (e) => {
      window.location.reload()
    })
  })
})

const controls = new OrbitControls(cam, renderer.domElement)

controls.update()

let targetPos = new THREE.Vector3(0, 1.5, 0)
let moveSpeed = 0.5

const random = () => {
  return Math.floor(Math.random() * 2)
}

console.log(random())

const move = (sword) => {
  
  sword.position.y += moveSpeed
  if (sword.position.y >= targetPos.y) {
    sword.position.y = targetPos.y
    sword.rotation.y = Math.PI / 2
    return
  }
  //requestAnimationFrame(animate)
  //cam.lookAt(sword)
}


const animate = () => {
  requestAnimationFrame(animate)

  controls.update()
  renderer.render(scene, cam)
}


animate()